import * as React from "react"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"

const PoolCareArticleTemplate = ({ data, location }) => {
  const post = data.markdownRemark
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const image = getImage(post.frontmatter.headerImage)
  return (
    <Layout location={location} title={siteTitle}>
      <Seo
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <article
        className="page-section about-heading"
        itemScope
        itemType="http://schema.org/Article"
      >
        <div className="container">
        <GatsbyImage
            className="img-fluid rounded about-heading-img mb-3 mb-lg-0 custom-box-shadow"
            image={image}
            quality={95}
            width={1000}
            placeholder="blurred"
            formats={["auto", "webp", "avif"]}
            alt="sun"
          />
          <div className="about-heading-content">
            <div className="row">
              <div className="col-xl-9 col-lg-10 mx-auto">
                <div className="bg-faded rounded p-5">
                  <header>
                    <h1 className="section-heading mb-4">
                      <span className="section-heading-upper">
                        {post.frontmatter.subtitle}
                      </span>
                      <span className="section-heading-lower">
                        {post.frontmatter.title}
                      </span>
                    </h1>
                  </header>
                  <section
                    dangerouslySetInnerHTML={{ __html: post.html }}
                    itemProp="articleBody"
                  />
                  <div className="intro-button mx-auto flex-column d-flex align-items-end">
                    <span></span>
                    <a className="btn btn-primary btn-xl" href="../store">
                      Visit Us Today!
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </article>
    </Layout>
  )
}

export default PoolCareArticleTemplate

export const pageQuery = graphql`
  query poolCareArticleBySlug($id: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        subtitle
        date(formatString: "MMMM DD, YYYY")
        description
        headerImage {
          childImageSharp {
            gatsbyImageData
          }
        }
        url
      }
    }
  }
`
